//vue
function convertTemplateAndControlData(layout, isPreView, options) {
  layout = dsf.mix(true, {}, layout);
  let defaultOptions = {
    before: null,
    after: null
  };
  dsf.mix(defaultOptions, options);
  let parentNodes = [];
  /**
   * nodeprops
   * parentprops
   * slot
   * isRepeat
   * prefixcaption
   * textProxy<span>
   */
  function createElement(node, parent, slot, slotScope, scopeDataKey, unique, isRepeat, prefix, textProxy) {
    let arr = [],
      props = {};

    defaultOptions.before && defaultOptions.before(node, parentNodes, slot, slotScope, scopeDataKey);
    if (node) {
      parentNodes.push(node);
    }
    let isFormControl = node.isFormControl;
    let metadataFullCode = node.metadata_fullcode;
    let modelExpress = "$viewData";
    let s = []
    if (slotScope) {
      s.push(slotScope);
    }
    if (scopeDataKey) {
      s.push(scopeDataKey)
    }
    if (s.length) {
      modelExpress = s.join(".")
    }
    // let modelExpress = (isRepeat) ? (slotScope ? slotScope + (scopeDataKey ? "." + scopeDataKey : "") : "") : "$viewData"
    let attachAttrs = {};
    if (isFormControl && metadataFullCode) {
      attachAttrs['v-model'] = modelExpress + "['" + metadataFullCode + "']";
      //items
      if (node.metadata.ckey == "items-meta-data" && !isPreView) {
        attachAttrs[":items"] = "$enum['" + metadataFullCode + "']";
      }
    }
    if (node.treeProxy) {
      _.each(node.slots, (s, i) => {
        if (s.controls.length > 0) {
          _.each(s.controls, (c, ii) => {
            let child = createElement(c, parent, { slotName: s.name, slotIndex: i, $index: ii }, slotScope, scopeDataKey, unique, isRepeat, prefix, textProxy);
            arr.push(...child.tpl);
            dsf.mix(props, child.props);
          });
        }
      });
    }
    //HTMLhtml 
    else if (node.ctrlName == 'DsfHtml' && !isPreView) {
      if (node.html) {
        arr.push(dsf.unescapeHTML(node.html));
      }
    } else {
      let ref = "";
      if (!isRepeat) {
        attachAttrs['v-bind.sync'] = "$controls['" + node.designId + "']";
        if (node.caption) {
          ref = "'" + node.caption + "'"
        }
        attachAttrs[':local'] = modelExpress
      } else {
        // attachAttrs['v-bind'] = "updateControlPropsInTemplate('" + node.designId + "',scope)";
        attachAttrs['v-bind'] = `$controls['${node.designId}-'+${modelExpress}.${unique||'$hash'}]`;
        attachAttrs[':local'] = modelExpress;
        attachAttrs[':design-id'] = "'" + node.designId + "'";
        // attachAttrs[":inSubTemplate"] = "true";
      }

      attachAttrs[":key"] = "'" + node.designId + "'";

      if (ref) {
        attachAttrs[':ref'] = ref;
        attachAttrs[':ctrl-id'] = ref;
      }
      if (node.isView) {
        // attachAttrs[":is-loaded"] = "isLoaded";
        attachAttrs["v-if"] = "$isLoaded";
        // attachAttrs["v-else"] = "";
        attachAttrs['v-loading.fullscreen'] = "loading";
      }
      let attrMap = getVuePropsAndEventMap(attachAttrs);
      let customAttrs = !isPreView ? getVuePropsAndEventMap(node.development['attrs'] || {}) : {};
      let mergeAttrs = { ...attrMap, ...customAttrs };
      let attrsArr = _.map(mergeAttrs, (attr) => {
        let name = attr.prefix + attr.propName + attr.modifier;
        return name + "=" + attr.value;
      });
      //
      // let eventMap = !isPreView ? getVuePropsAndEventMap(node.development['events']) : {};
      // let eventArr = _.map(eventMap, (attr) => {
      //   let name = (attr.prefix || "@") + attr.propName + attr.modifier;
      //   return name + "=" + attr.value;
      // });
      let _textProxy = node.isFormControl && textProxy;
      if (_textProxy) {
        arr.push(`<template v-if="!${modelExpress+'.$editing'}">`);
        let exp = `${modelExpress+ "['" + metadataFullCode + "']"}`;
        arr.push(`<span :title="$dataFormat('proxyTitle','${node.formatter}',${exp})" v-html="$dataFormat('proxy','${node.formatter}',${exp})"></span>`);
        arr.push("</template>")
        arr.push("<template v-else>")
      }
      arr.push("<" + node.ctrlName + " " + attrsArr.join(" ") + ">");
      _.each(node.slots, (s, i) => {
        if (s.controls.length > 0) {
          let templateMark = true;
          if (s.name == "default" && !s.scope) {
            templateMark = false;
          }
          if (templateMark) {
            arr.push("<template v-slot:" + s.name + (s.scope ? "=" + JSON.stringify(s.scope) : "") + ">")
          }
          let repeat = s.repeat === true ? true : false;
          if (isRepeat) {
            repeat = true;
          }
          let prefix = s.repeat === true ? node.caption : "";
          let dataKey = s.dataKey || scopeDataKey;
          let scopeName = s.scope || slotScope;
          let textProxy = s.textProxy; //dsf.isDef(s.textProxy)?s.textProxy:_textProxy
          let unique = s.unique || unique;
          _.each(s.controls, (c, ii) => {
            let child = createElement(c, node, { slotName: s.name, slotIndex: i, $index: ii }, scopeName, dataKey, unique, repeat, prefix, textProxy);
            arr.push(...child.tpl);
            dsf.mix(props, child.props);
          });
          if (templateMark) {
            arr.push("</template>");
          }
        }
      });
      arr.push("</" + node.ctrlName + ">");
      if (textProxy) {
        arr.push("</template>")
      }
      props[node.designId] = node;
    }
    //div,
    // if (node.isView) {
    //   arr.splice(0, 0, '<div v-if="!isLoaded"></div>');
    // }
    defaultOptions.after && defaultOptions.after(node, parentNodes, slot, slotScope, scopeDataKey)
    parentNodes.pop();
    return {
      props: props,
      tpl: arr
    };
  }
  let result = createElement(layout, null)
  return {
    props: result.props,
    tpl: result.tpl.join("\n")
  }
}

function getVuePropsAndEventMap(props) {
  let attrMap = {};
  _.each(props, (s, i) => {
    // let attr = attachAttrs[i];
    var propExpressArray = i.split(".");
    let rexpVueProp = /(:|v-bind:?|v-on:?|@)?([^\.:]*)(\..+)?/g;
    let propName = "",
      prefix = "",
      modifier = "";
    let group = rexpVueProp.exec(i)
    if (group && group.length > 1) {
      propName = group[2];
      prefix = group[1];
      modifier = group[3];
    }
    if (dsf.type(s) != "string" && (propExpressArray[0].startsWith(":") || propExpressArray[0].startsWith("v-bind:"))) {
      s = "" + s;
    }
    let attr = {
      propName: (propName || ""),
      prefix: (prefix || ""),
      modifier: (modifier || ""),
      value: JSON.stringify(s)
    }
    attrMap[propName || prefix] = attr;
  });
  return attrMap;
}

//scss
function compileCss(scssStr, nameSpace) {
  return new Promise((resolve, reject) => {
    var sass = new Sass();
    var scss = [
      ".ds-page" + (nameSpace ? '[module-name="' + nameSpace + '"]' : "") + "{",
      scssStr,
      "}",
    ];
    var files = dsf.config.designer.scss || [];
    sass.preloadFiles(dsf.config.designer.scssDir + "pc/scss", "", files, () => {
      let scssFiles = _.map(files, (file) => `@import "${file}";`);
      scss = [...scssFiles, ...scss];
      sass.compile(scss.join("\n"), function(result) {
        if (result.status == 0) {
          let cssStr = dsf.beautify.css(result.text, {
            indent_size: "2",
            indent_char: " ",
          });
          resolve(cssStr);
        } else {
          let errorMsg = ":" + result.message;
          dsf.error(errorMsg);
          reject(errorMsg);
        }
      });

    });
  });
}

function getMetaDataPrefixById(id) {
  let arr = id.split(".");
  arr = arr.slice(0, arr.length - 1);
  return arr.join("_");
}

//controls,currentControldsf.
function checkMetadataControlValue(metadata) {
  if (metadata.currentControl && metadata.currentControl.value) {
    if (!metadata.currentControl.value.startsWith('dsf.')) {
      metadata.currentControl.value = "dsf." + metadata.currentControl.value;
    }
  }
  _.each(metadata.controls, (ctrl) => {
    if (!ctrl.value.startsWith('dsf.')) {
      ctrl.value = "dsf." + ctrl.value;
    }
  })
}

// function closestPage(this){

// }

var skipMap = {
  'Math': 1,
  'Date': 1,
  'Number': 1,
  'String': 1,
  'Boolean': 1,
  'Object': 1,
  '$event': 1,
  'window': 1,
  '__vmodel__': 1,
  '__local__': 1,
  'scope': 1,
  '_self': 1,
  'JSON': 1,
  'this': 1,
  'true': 1,
  'false': 1,
  'dsf': 1
};
let stringNum = 0;
let stringPool = {
  map: {}
};
let rlocalVar = /[$a-zA-Z_][$a-zA-Z0-9_]*\(?/g;
var rvmKey = /(^|[^\w\u00c0-\uFFFF_])(@|##)(?=[$\w])/g;
var robjectProp = /\.[\w\.\$]+/g; // el.xxx xxx
var rregexp = /(^|[^/])\/(?!\/)(\[.+?]|\\.|[^/\\\r\n])+\/[gimyu]{0,5}(?=\s*($|[\r\n,.;})]))/g;
var rshortCircuit = /\|\|/g;
var ruselessSp = /\s*(\.|\|)\s*/g;
var rpipeline = /\|(?=\?\?)/;
var rfill = /\?\?\d+/g;
var rfilterName = /\|(\w+)/g;
var robjectKey = /(\{|\,)\s*([\$\w]+)\s*:/g; // {xxx:1,yyy: 2}xxx, yyy

function addScope(expr) {
  stringPool.map = {};
  var input = expr.replace(rregexp, function(a, b) {
    return b + dig(a.slice(b.length));
  }); //
  input = clearString(input); //

  input = input.replace(rshortCircuit, dig) //
    .replace(ruselessSp, '$1') //.|
    .replace(robjectKey, function(_, a, b) {
      //
      return a + dig(b) + ':'; // ms-widget="[{is:'ms-address-wrap', $id:'address'}]" 
    })
    .replace(rvmKey, '$1__vmodel__.') //@##__vmodel__.
    .replace(rfilterName, function(a, b) {
      //
      return '|' + dig(b);
    });

  input = addScopeForLocal(input); //__vmodel__
  var filters = input.split(rpipeline); //
  var body = filters.shift().replace(rfill, fill).trim();
  if (/\?\?\d/.test(body)) {
    body = body.replace(rfill, fill);
  }
  return body;

}

function addScopeForLocal(str) {
  let props = str.replace(robjectProp, dig);
  let result = props.replace(rlocalVar, function(el /*, a, c, d*/ ) {
    if (!skipMap[el]) {
      let isFunction = false;
      if (el.endsWith("(")) {
        isFunction = true;
      }
      if (isFunction) {
        let fnName = el.replace("(", "");
        return "$proxyFn('" + fnName + "',";
      }
    }
    return el;
  });
  return result;
}

function dig(a) {
  var key = '??' + stringNum++;
  stringPool.map[key] = a;
  return key + ' ';
}

function fill(a) {
  var val = stringPool.map[a];
  return val;
}

function clearString(str) {
  var array = readString(str);
  for (var i = 0, n = array.length; i < n; i++) {
    str = str.replace(array[i], dig);
  }
  return str;
}

function readString(str, i, ret) {
  var end = false,
    s = 0,
    i = i || 0;
  ret = ret || [];
  for (var n = str.length; i < n; i++) {
    var c = str.charAt(i);
    if (!end) {
      if (c === "'") {
        end = "'";
        s = i;
      } else if (c === '"') {
        end = '"';
        s = i;
      }
    } else {
      if (c === end) {
        ret.push(str.slice(s, i + 1));
        end = false;
      }
    }
  }
  if (end !== false) {
    return readString(str, s + 1, ret);
  }
  return ret;
}

