import utils from "./utils";

let metadataMap = {};

let dataType = [
  { text: "", value: "string" },
  { text: "", value: "date" },
  { text: "", value: "number" },
  { text: "", value: "object" },
  { text: "", value: "time" },
  { text: "", value: "longObject" }
];

let mmtype = function(num) {
  let v = {
    "1": { value: "1", text: "" },
    "2": { value: "2", text: "" },
    "3": { value: "3", text: "" }
  };
  return v[num];
};

let getDataType = function(type) {
  return _.find(dataType, v => {
    return v.value == type;
  });
};


// //  express: "",
//       expressName:"",
//       isNumber: false,
//       max: "",
//       min: "",
//       precision: "",
//       required: false,
//       errorMsg: {}

let validate = function() {
  return null;
};

function add(key, metadata) {
  key = utils.kebabCase(key);
  if (!metadataMap[key]) {
    metadataMap[key] = metadata;
  } else {
    dsf.warn(key + "");
  }
  return metadataMap[key];
}

function get(key, obj) {
  key = utils.kebabCase(key);
  let m = metadataMap[key](dsf);
  m.ckey = key;
  return dsf.mix(m, obj || {});
}

function use(obj) {
  for (let k in obj) {
    add(k, obj[k]);
  }
}

function all() {
  return metadataMap;
}


export default {
  add,
  get,
  all,
  use,
  mmtype,
  dataType,
  getDataType,
  validate
};