import control from "./control";

export default {
  mixins: [control],
  props:{
    //
    isTextContent:{
      type: Boolean,
      default: false
    },
    value: {
      type: [String, Number],
      default: ''
    },
    defaultValue: {
      type: [String, Number],
      default: ''
    }
  },
  design: {
    //
    isMask: false
  },
  data () {
    return {
      $value: ''
    }
  },
  watch: {
    'value' (to) {
      if (!to && this.defaultValue) {
        // todo Express
        // let exp = dsf.Express;
        // this.$value = exp.replace(this.defaultValue.text);
        this.$value = this.defaultValue;
      }
    }
  }
};
