import control from "./control";

const VALUE_CHANGE = "model-value-mutation";
const VALUE_UPDATE_SYNC = "update:value";

/**
 * @class FormControl
 * @description 
 * @augments Control
 * @example
 * import formControl from "./formControl";
 *
 * export default {
 *    name: "xxxx",  name
 *    ctrlCaption: "xxx", // 
 *    mixins: [formControl],
 *    ...
 * }
 */
export default {
  model: {
    prop: "value",
    event: VALUE_CHANGE,
  },
  /**
   * @memberof FormControl
   * @name Props 
   * @property {Boolean} [isFormControl = true] 
   * @property {String} [label = ""] 
   * @property {Boolean} [showLabel = true] label
   * @property {Boolean} [readOnly = false] 
   * @property {Boolean} [disabled = false] 
   * @property {Boolean} [allowPost = true] 
   * @property {Boolean} [required = false] 
   * @property {String|Number|Object|Array|Boolean} [value = null] {text:'',value:''}
   * @property {String} [dataCapion = ""] 
   * @property {String} [dataAudioUrl = ""] mp3
   * @property {String} [dataVideoUrl = ""] mp4
   * @property {Object} [metadata = null] 
   * @property {String} [readOnlyShowText = text] 
   * @property {String} [metadata_fullcode = ""] 
   * @property {Number} [metaDataLevel = 0] 01
   * @property {String} [validateRule = ""] 
   * @property {Array} [errors = []] 
   */
  props: {
    //
    isFormControl: {
      type: Boolean,
      default: true,
    },
    //
    label: {
      type: String,
      default: "",
    },
    //label
    showLabel: {
      type: Boolean,
      default: true,
    },
    //
    readOnly: {
      type: Boolean,
      default: false,
    },
    //
    disabled: {
      type: Boolean,
      default: false,
    },
    //
    allowPost: {
      type: Boolean,
      default: true,
    },
    //
    required: {
      type: Boolean,
      default: false,
    },
    //{text:'',value:''}
    value: {
      type: [String, Number, Object, Array, Boolean],
      default () {
        return null;
      },
    },
    //
    dataCapion: {
      type: String,
      default: "",
    },
    //mp3
    dataAudioUrl: {
      type: String,
      default: "",
    },
    //mp4
    dataVideoUrl: {
      type: String,
      default: "",
    },
    //
    metadata: {
      type: Object,
      default () {
        return null;
      },
    },
    //
    readOnlyShowText: {
      type: String,
      default: "text",
    },
    //
    metadata_fullcode: {
      type: String,
      default: "",
    },
    //
    simple: {
      type: Boolean,
      default: false
    },
    //01
    metaDataLevel: {
      type: Number,
      default: 0,
    },
    //
    formatter: {
      type: String,
      default: ""
    },
    //
    validateRule: {
      type: String,
      default: "",
    },
    //
    isValidate: {
      type: Boolean,
      default: true,
    },
    //
    colSpan: {
      type: Number,
      default: 24
    },
    labelWidth: {
      type: Number,
      default: 120
    },
    // 
    immediatelyAudit: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      errorsCopy: []
    }
  },
  mixins: [control],
  computed: {
    errors: {
      get() {
        if (!this.isValidate) {
          this.errorsCopy = [];
        }
        return this.errorsCopy;
      },
      set(value) {
        if (this.isValidate) {
          this.errorsCopy = value;
        }
      }
    },
    getCss() {
      return [
        this.errors.length > 0 ? 'ds-error' : '',
        ...this.$getClass(),
        this.showLabel ? "ds-has-label" : "",
        this.required ? "ds-required" : "",
        this.readOnly ? "ds-readonly" : "",
      ];
    },
    errorsMsg() {
      return _.map(this.errors, "message").join(",");
    },
    $labelWidth() {
      if (this.$page) {
        return this.$page.formItemLabelWidth || this.labelWidth;
      }
      return this.labelWidth;
    }
  },
  created() {
    if (!this.isDesign && this.$form) {
      this.$form.addFiled(this);
    }
    if (!this.isDesign && this.$dynamicForm) {
      this.$dynamicForm.addFiled(this);
    }
  },
  mounted() {},
  beforeDestroy() {
    if (!this.isDesign && this.$form) {
      // console.log(""+this.caption)
      this.$form.removeFiled(this);
      // this.$form.$emit("form.removeField", this);
    }
    if (!this.isDesign && this.$dynamicForm) {
      this.$dynamicForm.removeFiled(this);
    }
  },
  /**
   * @memberof FormControl
   * @name Event 
   * @property {Function} valueMutation  [value-mutation]
   * @example
   * <components @value-mutation="CallbackFn" ></components>
   * //  valueMutation   - 
   */
  methods: {
    showHelper() {
      this.$openDialog({
        title: "",
        template: `<div v-html="params.value"></div>`,
        width: "30vw",
        height: "50vh",
        params: {
          value: this.dataCapion
        }
      })
    },
    getLableWidth() {
      return this.labelWidth || 120;
    },
    getLabelWidthStyle() {
      if (this.$labelWidth && this.showLabel) {
        return {
          "width": this.$labelWidth + "px"
        }
      }
      return {}
    },
    getFormItemBlockMarginLeft() {
      if (this.$labelWidth && this.showLabel) {
        return {
          "margin-left": this.$labelWidth + "px"
        }
      }
      return {}
    },
    getShowContent() {
      if (dsf.type(this.value) == "object") {
        return this.value[this.readOnlyShowText];
      } else if (dsf.type(this.value) == "array") {
        return _.map(this.value, this.readOnlyShowText).join(",");
      } else {
        return this.value;
      }
    },
    $emitValueChange(v) {
      //v-model:value.sync
      let listeners = this.$listeners || {};
      //v-model
      if (listeners[VALUE_CHANGE]) {
        this.$emit(VALUE_CHANGE, v);
      }
      //value.sync
      else if (listeners[VALUE_UPDATE_SYNC]) {
        this.$emit(VALUE_UPDATE_SYNC, v);
      }
    },
    emitFormValidate(v) {
      if (this.$form) {
        this.$form.$emit("form.validateField", { value: v || this.value, target: this });
      }
    },
    emitValueChange(v) {
      this.$emitValueChange(v);
      this.$dispatch("value-change", v);
    }
  },
  design: {
    infoButton: true,
    layout: {
      exclude: ["value"]
    },
    metadata: {
      create(root) {
        if (!this.metadata) {
          this.metadata = {};
        }
        return {
          self: this,
          init: null,
          metadata: this.metadata,
        };
      },
    },
  },
};