/**
 * @class Control
 * @description 
 * @example 
 * import control from "./control";
 * 
 * export default {
 *    name: "xxxx",  name
 *    ctrlCaption: "xxx", // 
 *    mixins: [control],
 *    ...
 * }
 */
export default {
  // inheritAttrs:false,
  inject: {
    $designer: {
      default: null
    },
    $vm: {
      default: null
    },
    $page: {
      default: null
    },
    $form: {
      default: null
    },
    $templateControl: {
      default: null
    },
    $list: {
      default: null
    },
    $dynamicForm: {
      default: null
    },
    $layoutTree: {
      default: null
    },
    $isMobile: {
      default: false
    }
  },
  comments: true,
  /**
   * @memberof Control
   * @name Props 
   * @property {Boolean} [isDesign = false] 
   * @property {Object} [layoutRoot = null] page
   * @property {Object} [layoutParent = null] 
   * @property {String} [designId = ""] id
   * @property {String} [caption = ""] 
   * @property {Boolean} [visible = true] 
   * @property {Object} [metadata = null] 
   * @property {Array} [classList = []] 
   * @property {Array} [classList = []] 
   * @property {Boolean} [noPadding = false] 
   * @property {Array} [slots = []] 
   * @property {Boolean} [isMergeHttp = false] 
   * @property {Boolean} [tbNoPadding = false] 
   * @property {Boolean} [lrNoPadding = false] 
   * @property {Object} [local = null] 
   * @property {Boolean} [inSubTemplate = false] 
   * @property {ControlDevelopment} [development = {events: {},attrs: {}}] 
   * @property {Boolean} [designProxy = false] 
   * @property {Boolean} [designOnlySlot = false] 
   */
  /**
   * @namespace ControlDevelopment
   * @description 
   * @property {Object} [events = {}] 
   * @property {Object} [attrs = {}] 
   */
  props: {

    //
    isBusiness: {
      type: Boolean,
      default: false
    },
    //
    isDesign: {
      type: Boolean,
      default: false
    },
    //id
    designId: {
      type: String,
      default: ""
    },
    //
    caption: {
      type: String,
      default: ""
    },
    //
    visible: {
      type: Boolean,
      default: true
    },
    //
    metadata: {
      type: Object,
      default () {
        return null;
      }
    },
    //
    classList: {
      type: Array,
      default () {
        return [];
      }
    },
    noPadding: {
      type: Boolean,
      default () {
        return false;
      }
    },
    //
    slots: {
      type: Array,
      default () {
        return [];
      }
    },
    isMergeHttp: {
      type: Boolean,
      default: false
    },
    //
    tbNoPadding: {
      type: Boolean,
      default: false
    },
    //
    lrNoPadding: {
      type: Boolean,
      default: false
    },
    local: {
      type: Object,
      default () {
        return null
      }
    },
    //
    // inSubTemplate: {
    //   type: Boolean,
    //   default: false
    // },
    //
    development: {
      type: Object,
      default () {
        return {
          // events: {},
          attrs: {}
        }
      }
    },
    /* ==================, ==================*/
    //
    designProxy: {
      type: Boolean,
      default: false
    },
    //
    designOnlySlot: {
      type: Boolean,
      default: false
    }
    /* ==================, ================== */
  },
  computed: {
    getCss() {
      return this.$getClass();
    },
    props() {
      let _vm = this.$vm;
      if (_vm && !this.isDesign) {
        let key = "";
        if (this.designId) {
          if (isListRepeat(this)) {
            key = this.designId + "-" + (this.local.$hash || this.local._id || this.local.$index);
          } else {
            key = this.designId;
          }
          return _vm.$controls[key];
        }
        return null;
      }
    },
    isDes() {
      return !!this.designId;
    },
    queryString() {
      let vmQuery = this.$vm ? this.$vm.queryString : null;
      let routerQuery = this.$route.query;
      let qs = dsf.mix({}, routerQuery, vmQuery);
      return qs;
    }
  },
  data() {
    return {};
  },
  beforeCreate() {},
  /**
   * @memberof Control 
   * @name LifeCycle 
   * @description 
   * @example 
   * //  -
   * beforeDestroy -> before-destroy 
   * @property {Function} created 
   * @property {Function} mounted 
   * @property {Function} updated 
   * @property {Function} beforeDestroy [before-destroy]
   * @property {Function} destroy 
   */
  created: function() {
    // ,props,props
    if (this.isDesign) {
      if (this.$props) {
        for (let k in this.$props) {
          propsProxy.call(this, this.$props, k);
        }
      }
    } else {
      if (isListRepeat(this)) {
        //$controls;
        let key = this.designId + "-" + this.local.$hash;
        if (!this.$vm.$controls[key]) {
          let props = dsf.mix(true, {}, this.$vm.$controls[this.designId]);
          if (this.$list && this.$list.updateSubControls) {
            let ctrlProps = this.$list.updateSubControls(props, this);
            if (ctrlProps) {
              props = ctrlProps;
            }
          }
          this.$vm.$set(this.$vm.$controls, key, props);
        }
      }
    }
    this.$dispatch("created", this);
  },
  mounted() {
    if (this.$el && this.$el.setAttribute) {
      this.$el.setAttribute("ctrl_type", this.$options.ctrlType);
    }
    if (this.isDesign && this.designId) {
      this.$el.setAttribute("des-id", this.designId);
    }
    this.$dispatch("mounted", this);
  },
  updated() {
    this.$dispatch("updated", this);
    // console.log(this.$options.name + "")
  },
  beforeDestroy() {
    if (!this.isDesign) {
      if (isListRepeat(this)) {
        //$controls;
        let key = this.designId + "-" + this.local.$hash
        this.$vm.$delete(this.$vm.$controls, key);
        if (this.$list && this.$list.removeSubControls) {
          this.$list.removeSubControls(this)
        }
      }
    }
    this.$dispatch("before-destroy", this);
  },
  destroyed() {
    if (this.viewContext && this.viewContext.viewData) {
      this.viewContext.viewData = null;
    }
    if (this.viewContext && this.viewContext.viewInitData) {
      this.viewContext.viewInitData = null;
    }
    this.$dispatch("destroy", this);
  },
  methods: {
    //
    // createMetaData() {},
    //
    $dispatch(name, args) {
      let event = { target: this, name: name, args: args };
      this.$emit(name, event);
    },
    $dispatchTo(componentName, name, args) {
      let event = { target: this, name: name, args: args };
      eventTrigger.dispatch.call(this, componentName, name, event);
    },
    removeClass(keys) {
      keys = dsf.type(keys) == "array" ? keys : [keys];
      _.each(keys, k => dsf.array.remove(this.classList, k));
    },
    addClass(keys) {
      keys = dsf.type(keys) == "array" ? keys : [keys];
      _.each(keys, k => dsf.array.ensure(this.classList, k));
    },
    clearClass() {
      _.eachRight(this.classList, (k, i) => {
        dsf.array.removeAt(this.classList, i);
      });
    },
    closest(componentName) {
      var parent = this.$parent || this.$root;
      var name = parent.$options._componentTag;
      while (parent && (!name || name !== componentName)) {
        parent = parent.$parent;
        if (parent) {
          name = parent.$options._componentTag;
        }
      }
      if (parent) {
        return parent;
      }
      return null;
    },
    postBefore() {
      return new Promise((resolve, reject) => {
        resolve()
      });
    },
    $getClass() {
      return [
        ...this.classList,
        !this.visible ? (this.isDesign ? 'ds-design-hide' : 'ds-hide') : "",
        this.tbNoPadding ? 'ds-top-bottom-no-padding' : '',
        this.lrNoPadding ? 'ds-left-right-no-padding' : ''
      ];
    },
    $getSlot(name) {
      let result = _.filter(this.slots, (s) => s.name == name);
      return result[0] ? result[0] : null;
    }
  },
  design: {
    //
    isMask: true,
    //
    infoButton: false,
    //
    dragButton: true,
    //
    removeButton: true,
    //
    fit: false,
    layout: {
      isDeep: true,
      exclude: [],
      //
      nameValidate: true,
      //
      accpetContextData: false,
      attachAttributes: {},
      attachSlot: []
    },
    metadata: {
      isDeep: true,
      created() {}
    },

  }
};

function isListRepeat(ctrl) {
  if (ctrl.$vm && ctrl.local && dsf.hasOwn(ctrl.local, '$hash') && ctrl.designId) {
    return true;
  }
  return false;
}

function propsProxy(props, key) {
  let _this = this;
  Object.defineProperty(this, key, {
    set: function(v) {
      _this.$emit("update:" + key, v);
    },
    get: function() {
      return _this.$props[key];
    }
  });
}

// eslint-disable-next-line no-unused-vars
let eventTrigger = {
  broadcast(componentName, eventName, params) {
    this.$children.forEach(child => {
      var name = child.$options._componentTag;

      if (name === componentName) {
        child.$emit.apply(child, [eventName].concat(params));
      } else {
        broadcast.apply(child, [componentName, eventName].concat([params]));
      }
    });
  },
  dispatch(componentName, eventName, params) {
    var parent = this.$parent || this.$root;
    var name = parent.$options._componentTag;

    while (parent && (!name || name !== componentName)) {
      parent = parent.$parent;

      if (parent) {
        name = parent.$options._componentTag;
      }
    }
    if (parent) {
      parent.$emit.apply(parent, [eventName].concat(params));
    }
  }
};